.todoform {
    background-color: white;
    border-radius: 8px;
    padding: 20px;
    width: 500px;
    margin: 0 auto;
    margin-top: 50px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.modernInput {
    border: none;
    width: 100px;
    display: block;
    color: black;
    font-size: 16px;

}

.modernInput {
    outline: none;
}

.modernButton {
    display: inline-block;
    border: none;
    border-radius: 4px;
    background-color: #f7ca18;
    padding: 12px 20px;
    color: #ffffff;
    cursor: pointer;
}

.inputContainer {
    display: flex;
}